<template>
    <div class="section-wrapper">
        <div class="profile-item intro-section">
            <h1>
                <span id="greeting" :key="greetingKey">{{ greetings[currentGreetingIndex] }}</span>
                ! This is Yachi's Website.
            </h1>
        </div>
        <div class="profile-item icon-section" >
            <div class="icon-container"  v-for="(value, key) in myProfile" :key="key">
                <div :id="key">
                    <a :href="value" target="_blank">
                        <img v-if="key == 'github' " :src="github_icon">
                        <img v-if="key == 'resume' " :src="resume_icon">
                    </a>
                </div>
            </div>
        </div>
    </div>
</template>

<script>
import github_icon from "@/assets/svg/github.svg"
import resume_icon from "@/assets/svg/resume.svg"
export default{
    name: 'ProfileSection',

    data() {
        return{
            myProfile: {
                github: "https://github.com/KimmyYo/",
                resume: "https://docs.google.com/document/d/1Hk3GyYPSRMK_LTKOpuE_ovbA368NmiXDWyVCwZRbUSY/edit?usp=sharing"
            },
            github_icon: github_icon,
            resume_icon: resume_icon,
            greetings: ['Hello', 'Hola', 'Bonjour', 'Ciao', '你好', 'こんにちは'],
            currentGreetingIndex: 0,
            greetingKey: 0,
        }
    },
    mounted(){
        setInterval(this.changeGreeting, 2000);
    },
    methods:{
        changeGreeting() {
            console.log(this.greetingKey);
            // Update the greeting to the next one in the array
            this.currentGreetingIndex = (this.currentGreetingIndex + 1) % this.greetings.length;
            // Update the key to trigger the transition
            this.greetingKey += 1;
        },
    }
}
</script>


