<template>
  <nav>
    <NavbarSection />
  </nav>
  <router-view></router-view>
  <!-- <ProfileSection />
  <IntroductionSection />
  <ExperienceSection />
  <ProjectSection />
  <OthersSection /> -->
  <footer>
    <div v-if="showButton">
      <button @click="handleButtonClick"><IconChevronDown /></button>
    </div>
    <div v-else class="footer-edit">
      <hr />
      <div>&copy; Yachi Yu</div>
    </div>
  </footer>
  
</template>

<script>
import NavbarSection from './components/NavbarSection.vue';
import { IconChevronDown } from '@tabler/icons-vue';

export default{
    components: {
      NavbarSection,
      IconChevronDown
    },
    mounted() {
      this.handleButtonClick()
    },
    methods: {
        handleButtonClick() {
          if(this.$route.path == "/home"){
              this.$router.push('/about');
          }
          if(this.$route.path == "/about"){
              this.$router.push('/project');
          }
          if(this.$route.path == "/project"){
              this.$router.push('Experience');
          }
          if(this.$route.path == "/experience" || this.$route.path == "/Experience"){
              this.$router.push('/others');
          }
          
      },
    },
    computed: {
        showButton() {
          // Condition to determine whether to show the button
          return this.$route.path !== '/others';
        },
    },
}

</script>
